.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 10px;
  padding-right: 10px;
}
@media (min-width: 960px) {
  .container {
    width: 960px;
  }
}
.button {
  display: block;
  font-family: inherit;
  text-decoration: none;
}
.text-field {
  width: 100%;
  padding: 15px 20px;
  border: 2px solid #e5e5e5;
}
.form-text {
  line-height: 30px;
  text-transform: uppercase;
  color: #d22856;
}
@media (max-width: 659px) {
  .form-text {
    display: block;
    padding-left: 22px;
    padding-bottom: 5px;
  }
