body {
  min-width: 320px;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 30px;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
input,
textarea {
  font: inherit;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.page-header {
  background-color: #283645;
}
.page-header .container {
  padding: 0;
  height: inherit;
}
.page-header__logo {
  display: block;
  width: 150px;
  padding: 21px 10px 16px 20px;
}
.page-header__logo-image--desktop {
  display: none;
}
.page-header__logo-image--tablet {
  display: none;
}
.page-header--closed {
  background-color: rgba(0, 0, 0, 0.3);
}
.page-header__logo {
  fill: #ffffff;
}
.page-header__logo:hover .page-header__logo-image {
  fill: #d22856;
}
.page-header__logo:active .page-header__logo-image {
  fill: #ffffff;
  opacity: 0.3;
}
@media (min-width: 660px) {
  .page-header__logo {
    padding: 12px 19px;
    width: 190px;
    height: 73px;
    margin-bottom: 20px;
  }
  .page-header .container {
    width: 100%;
    min-height: 110px;
    padding-top: 15px;
  }
  .page-header__logo-image {
    width: 147px;
    height: 40px;
    fill: #ffffff;
  }
  .page-header__logo-image--mobile {
    display: none;
  }
  .page-header__logo-image--tablet {
    display: block;
  }
  .page-header-wrapper {
    padding: 0;
  }
}
@media (min-width: 960px) {
  .page-header__logo {
    padding: 10px;
    width: 180px;
    height: 70px;
    margin-top: 13px;
  }
  .page-header__logo-image {
    display: block;
  }
  .page-header__logo-image--desktop {
    display: block;
  }
  .page-header__logo-image--tablet {
    display: none;
  }
  .page-header__logo-image--mobile {
    display: none;
  }
  .page-header {
    position: relative;
    z-index: 1;
    height: 110px;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .page-header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .page-header .container {
    width: 960px;
    padding-top: 0;
  }
}
.main-nav {
  background-color: #283645;
  border-top: 1px solid #000000;
}
.main-nav__toggle {
  position: absolute;
  right: 0;
  top: 0;
  width: 75px;
  height: 68px;
  padding: 10px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.main-nav__toggle-icon {
  fill: #ffffff;
}
.main-nav__toggle-icon:hover {
  fill: #d22856;
}
.main-nav__toggle-icon:active {
  fill: #d22856;
  opacity: 0.3;
}
.main-nav__toggle-icon--burger {
  display: none;
}
.main-nav--closed .main-nav__toggle-icon--cross {
  display: none;
}
.main-nav--closed .main-nav__toggle-icon--burger {
  display: inline-block;
}
.main-nav .site-list {
  display: block;
}
.main-nav--closed .site-list {
  display: none;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--closed {
  border-top: none;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 25px;
  }
  .main-nav--closed .site-list {
    display: none;
  }
  .main-nav--closed .main-nav__toggle {
    top: 22px;
    right: 0;
  }
}
@media (min-width: 960px) {
  .main-nav .site-list {
    display: flex;
    flex-wrap: wrap;
  }
  .main-nav {
    background-color: transparent;
    border: none;
  }
  .main-nav__toggle {
    display: none;
  }
}
.site-list {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-list__link {
  display: block;
  padding: 15px 20px 15px;
  font-size: 18px;
  line-height: 32px;
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center;
}
.site-list a[href]:hover {
  color: #d22856;
}
.site-list a[href]:active {
  color: #d22856;
  opacity: 0.3;
}
.site-list__item {
  border-bottom: 1px solid #000000;
  border-top: 1px solid #485461;
}
@media (min-width: 960px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  .site-list__item {
    margin: 0 30px;
    padding: 0 10px;
    border: none;
  }
  .site-list__item--active a {
    border-bottom: 2px solid #ffffff;
  }
  .site-list__item:last-of-type {
    margin-right: 0;
  }
  .site-list__link {
    padding: 3px 0;
    text-transform: none;
    font-weight: 400;
  }
}
