.page-header {
  background-color: #283645;
}

.page-header .container {
  padding: 0;
  height: inherit;
}

.page-header__logo {
  display: block;
  width: 150px;
  padding: 21px 10px 16px 20px;
}

.page-header__logo-image {
  &--desktop {
    display: none;
  }

  &--tablet {
    display: none;
  }
}

.page-header--closed {
  background-color: rgba(0, 0, 0, 0.3);
}

.page-header__logo {
  fill: #ffffff;

  &:hover .page-header__logo-image {
    fill: #d22856;
  }

  &:active .page-header__logo-image {
    fill: #ffffff;
    opacity: 0.3;
  }
}

@media (min-width: @tablet-width) {
  .page-header__logo {
    padding: 12px 19px;
    width: 190px;
    height: 73px;
    margin-bottom: 20px;
  }

  .page-header .container {
    width: 100%;
    min-height: 110px;
    padding-top: 15px;
  }

  .page-header__logo-image {
    width: 147px;
    height: 40px;

    fill: #ffffff;

    &--mobile {
      display: none;
    }

    &--tablet {
      display: block;
    }
  }

  .page-header-wrapper {
    padding: 0;
  }
}

@media (min-width: @desktop-width) {
  .page-header__logo {
    padding: 10px;
    width: 180px;
    height: 70px;
    margin-top: 13px;
  }

  .page-header__logo-image {
    display: block;

    &--desktop {
      display: block;
    }

    &--tablet {
      display: none;
    }

    &--mobile {
      display: none;
    }
  }

  .page-header {
    position: relative;
    z-index: 1;

    height: 110px;

    background-color: rgba(0, 0, 0, 0.3);
  }

  .page-header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .page-header .container {
    width: 960px;
    padding-top: 0;
  }
}

